<script>

import KetThuc from '../components/KetThuc.vue'
import Modau from '../components/Modau.vue'
import {Clicks} from '../data/Click' 
import LeftCom from '../components/Lefts.vue'
import RightCom from '../components/Right.vue'
export default {
  name: 'mieng-trau-ky-dieu',
  data: () => ({
    anh: 'https://cotich.net/uploads/news/a_621049624_mieng-trau-ky-dieu.jpg',
    per: -1,
    next: -1,
    ht: -1,
    ht2: -1,
    arrClassScc: ["", "", "", "", "", "", "", "", "", "", ""],
    kkk: 1
  }),
  metaInfo: {
    title: 'Miếng trầu kỳ diệu',
    meta: [
      { name: 'thumbnail', content: 'https://cotich.net/uploads/news/a_621049624_mieng-trau-ky-dieu.jpg' },
      { charset: 'utf-8' },
      { name: 'Keywords', content: 'Miếng trầu kỳ diệu' },
      { name: 'Description', content: 'Hồ Sinh với gia cảnh khá giả nhưng vẫn muốn lo lót để làm một chân thơ. Sau khi được giới thiệu đến hang đạo sỹ đã được mời ăn một miếng trầu và có giấc mơ kỳ lạ. Sau khi tỉnh dậy anh chàng đã vội vã trở về và từ đó chí thú làm ăn. ' },

      { itemprop: 'name', content: 'Truyện cổ tích' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1, maximum-scale=1' },
      { name: 'language', content: 'Vietnamese,vn' },
      { name: 'geo.region', content: 'VN' },
      { property: 'article:tag', content: 'Truyện cổ tích, Chuyện cổ tích' },
      { name: 'robots', content: 'index,follow' },
      { name: '', content: '' },
    ]
  },
  components: {
    LeftCom,
    RightCom,
    Modau,
    KetThuc
  },
  methods: {
     denmodau(){
      Clicks.clickmodau();
    },
    denketthuc(){
      Clicks.clickketthuc();
    },
    clicks(vts) {
      this.ht = vts + 1;
      this.ht2 = vts;
      this.per = vts - 1;
      this.next = vts + 1;
      if (this.ht % 2 === 0) {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], false);
        if (this.per > 0) {
          this.arrClassScc[this.per] = this.setClass(this.arrClassScc[this.per], false);
        }
      } else {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], true);
        if (this.next < this.arrClassScc.length) {
          this.arrClassScc[this.next] = this.setClass(this.arrClassScc[this.next], true);
        }
      }
      this.kkk++;
    },
    setClass(sClass, add) {
      var f = 'flipped';
      if (add) {
        if (sClass.indexOf(f) < 0) {
          sClass = sClass + " " + f;
        }
      } else {
        if (sClass.indexOf(" " + f) > 0) {
          sClass = sClass.replace(" " + f, "");
        } else
          if (sClass.indexOf(f) > 0) {
            sClass = sClass.replace(f, "");
          }
      }
      return sClass;
    },
    getCl(vt) {
      return this.arrClassScc[vt];

    },
    getZindex(vt) {
      if (vt % 2 === 0) {
        return "z-index: " + (4 - vt) + ";"
      } else {
        return "";
      }
    }
  },
  created() {
  }
}
</script>

