._v(" – Người ra có câu “Con trong lừ rưng rưng nước mắt, con ngoài lừ ngút ngoắt muốn vô”. Sao bác không giữ ruộng lại để cày cấy làm ăn, có hơn là phải quỵ lụy để mua mấy cái lo vào người cho khổ?"),_c('br'),_c('br'),_vm._v(" Nhưng sau mấy lần khuyên dỗ, vẫn thấy nét mặt bạn quả quyết quá, mới giới thiệu hắn với một người bạn khác của mình, và nói:"),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(4),style:(_vm.getZindex(4)),on:{"click":function($event){return _vm.clicks(4)}}},[_c('div',{staticClass:"box-content"},[_vm._v("– Người quen của tôi chả có thế lực gì đâu. Sẵn có quen một nhà đạo sĩ trên núi Ba Vì, ông ấy quen biết rất nhiều vị quyền cao chức trọng có thể hơn cả cụ Thượng nữa. Ông ấy có cách làm cho bác nên công danh. Để tôi viết mấy chữ, ông ấy sẽ vì tôi mà giúp bác hết sức."),_c('br'),_c('br'),_vm._v(" Hồ Sinh cầm thư của bạn tìm đường lên núi Ba Vì. Hắn hỏi thăm mãi, quả đến một cái hang có một phiến đá lớn lấp kín. Theo lời dặn, hắn kêu to: – “Có phải đây là hang đạo sĩ không? Nếu phải xin mở cửa cho vào”. Tự nhiên hòn đã xoay ra mở một lối cho hắn vào. Phía trong rất im lặng nhưng sáng sủa. Hắn bước quá chừng chục bước đã thấy vị đạo sĩ đang nằm trên một cái chõng, miệng nhai rầu, mắt lim dim nghỉ ngơi. Bên cạnh đó có một cái chõng khác, trên có một cơi trầu chỉ còn hai miếng Tuy có khách mà đạo sĩ cũng không ngồi dậy, chỉ với tay cầm lấy thư đọc, miệng vẫn nhai trầu bỏm bẻm. Một lát, nhìn trừng trừng vào mặt khách, nói:"),_c('br'),_c('br'),_vm._v(" – Ta sẽ cho anh được làm quan. Nào anh muốn làm quan to hay nhỏ, như thế nào?"),_c('br'),_c('br'),_vm._v(" Đáp:"),_c('br'),_c('br'),_vm._v(" – Tôi học hành cũng ít ỏi, chỉ muốn làm một chân thơ lại cũng đã mãn nguyện."),_c('br'),_c('br'),_vm._v(" – Được! Anh hãy ngồi nghỉ, ăn một miếng trầu, ta sẽ liệu."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(5),style:(_vm.getZindex(5)),on:{"click":function($event){return _vm.clicks(5)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Hồ Sinh rón rén lại ngồi ở giường lấy một miếng trầu trong cơi ra ăn. Miếng trầu rất ngon. Nhưng vừa nhai giập thì hắn đã thiu thiu ngủ."),_c('br'),_c('br'),_vm._v(" Sau khi trở về nhà mấy ngày, bỗng một hôm có một người lính lệ mang trát đến đòi. Hắn sợ quá tưởng có việc gì xảy ra. Nhưng khi vào dinh cụ Thượng, hắn được đón tiếp rất niềm nở. Người ta để dành cho hắn không phải là chân thơ lại ở huyện mà là một chân thông biện ở dinh quan Bố tại tỉnh nhà. Công việc chẳng có gì là khó khăn và tốn kém vì có “tay trong” của nhà đạo sĩ, làm cho hắn hết sức sung sướng. Thế là từ đó, Hồ Sinh hàng ngày ra vào công đường, dạ dạ, bẩm bẩm khúm núm trước mặt các quan. Ban đầu hắn thấy nhục, nhưng mỗi lần đứng trước mặt bọn tổng lý và những người dân có việc đến cửa quan thì hắn lại cho là một sự vinh hiển. Ban đầu hắn ngần ngại chối từ cả lễ bạt của những người có việc đưa lên lo lót, nhưng dần dần hắn bạo dạn và khôn ngoan hơn. Chẳng những hắn thành thạo trong nghề bóp nặn mà còn học được nhiều mánh khóe làm tiền kỳ lạ là tạo ra những vụ án bất ngờ mà kết quả cả nguyên cáo lẫn bị cáo tiền bạc xủng xoẻng dắt nhau đến công đường đút cho hắn và quan trên của hắn."),_c('br'),_c('br'),_vm._v(" Vì thế, trong vài ba năm, tiền của của hắn bộn bề, hắn làm nhà tậu ruộng và sống cuộc đời xa xỉ hơn trước. Hắn lại được một phú trưởng giả trong hạt gả con gái cho. Mười năm sau, vợ hắn đã sinh được hai trai hai gái và được cất nhắc làm một chức quan nhỏ. Một nhà phấn vua trang điểm, cuộc đời cứ thế lên như diều, không có ai theo kịp."),_c('br'),_c('br'),_vm._v(" Nhưng một ngày kia, giữa lúc Hồ Sinh đang ngồi cho vợ chải đầu thì bỗng có lính lệ cầm trát đến đòi. Hắn không nghi ngờ gì cả. Nhưng khi đến dinh cụ Thượng hắn liền bị bỏ ngục. Một viên khâm sai đặc phái cải trang đi thanh tra đã tìm ra được rất nhiều chứng cớ về những vụ tham tang hối lộ của bọn quan tỉnh, mà tất cả đều có liên quan đến hắn. Thế rồi, trong khi chờ đợi xử án thì những người dân bị vu oan giá họa ngày trước đều đổ xô tới quan khâm sai kiện hắn. Đơn kiện cao kể hàng chồng. Ngày xử án hắn là một ngày đông hơn hội. Hắn bị tử hình không đợi tâu về triều vì quan khâm sai có quyền “tiền trảm hậu tấu”. Trước khi ra pháp trường chịu tôi, hắn hồi tưởng lại chuyện cũ và ăn năn rằng phải chi mình đừng có lên hang đạo sĩ để nhờ lão ấy chạy chọt cho thì đâu đến nỗi này."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(6),style:(_vm.getZindex(6)),on:{"click":function($event){return _vm.clicks(6)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Hồ Sinh bỗng choàng dậy vì có một tiếng động rất dữ dội. Hắn mở mắt thì té ra mình vẫn còn nằm trên chiếc giường thứ hai của nhà đạo sĩ, chân đạp phải cơi trầu lăn xuống đá đánh choảng một tiếng, miếng trầu còn lại lăn lóc giữa đường. Còn đạo sĩ miệng vẫn còn nhai trầu, mắt lim dim, chợt ngồi dậy hỏi hắn:"),_c('br'),_c('br'),_vm._v(" – Bây giờ chúng ta sẽ bàn một chút. Anh sẽ cầm thư của tôi đến cụ Thượng…”"),_c('br'),_c('br'),_vm._v(" Nhưng lúc này Hồ Sinh không còn đủ can đảm để tính chuyện danh phận nữa. Hắn vội nhả miếng trầu đang ngậm ở miệng và cáo từ ra về. Từ đó, hắn trở nên một tay làm ăn chí thú trên ruộng đất của mình."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(7),style:(_vm.getZindex(7)),on:{"click":function($event){return _vm.clicks(7)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Nguồn: Truyện cổ tích Tổng hợp. ")])]),_c('div',{staticClass:"page",class:_vm.getCl(8),style:(_vm.getZindex(8)),on:{"click":function($event){return _vm.denketthuc()}}},[_c('KetThuc')],1)]):_vm._e()])])],1),_c('b-col',{attrs:{"cols":"2"}},[_c('RightCom')],1)],1)],1)
